* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  margin-bottom: 30px;
  font-size: 15px;
  line-height: 1em;
}

.timer {
  width: 540px;
  height: 170px;
  background-color: #b0f2ec;
  border-radius: 5px;
  padding: 20px;
  display: flex;
}

.field {
  width: 150px;
  background-color: #2d5fa4;
  border: 5px solid #74d3e8;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  line-height: 1em;
  display: flex;
  box-shadow: 0 -5px 10px #00000080;
}

.field:not(:last-child) {
  margin-right: 30px;
}

.value {
  color: #465255;
  box-shadow: #c850c0;
  background-image: linear-gradient(to right, #697acf 0%, #cb7cc6 46%, #dabf8e 100%);
  border-radius: 20px;
  margin-bottom: 10px;
  padding: 15px;
  font-size: 40px;
  line-height: 1em;
}

.label {
  color: #dabf8e;
  font-size: 20px;
  line-height: 1em;
}

